<template>
  <div class="section-container">
    <div class="flex-container">
        <div class="line">
          <div class="row">

            <span class="main-title">Hello, I'm Ollie.</span>
            <div class="title2">Computer Science Student</div>
          </div>
        </div>
    </div>
    <div class="space">

    </div>
    <button @click="scrollToSection('home')" data-tooltip="GitHub">
      <i class="fa-solid fa-arrow-down"></i>
    </button>

  </div>
</template>

<script>
import * as THREE from 'three';
import '@fortawesome/fontawesome-free/css/all.min.css';
import info from "../../info";


export default {
  name: "LaunchSection",
  data() {
    return {
      picture: info.flat_picture,
      description: info.description,
      linkedin: info.links.linkedin,
      github: info.links.github,
      resume: info.links.resume,
      scene: null,
      camera: null,
      renderer: null,
      nebula: null
    };
  },
  methods: {
    scrollToSection(sectionId) {
      const element = document.getElementById(sectionId);
      if (element) {
        element.scrollIntoView({ behavior: "smooth" });
      }
    },
    open(link) {
      switch (link) {
        case "linkedin":
          window.open(this.linkedin, "_blank");
          break;
        case "github":
          window.open(this.github, "_blank");
          break;
        case "resume":
          window.open(this.resume, "_blank");
          break;
      }
    },
  },
  mounted() {
    //* Source: https://github.com/mrdoob/three.js/blob/master/examples/webgl_buffergeometry_points.html

    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(27, window.innerWidth / window.innerHeight, 5, 3500);
    camera.position.z = 3250;
    const renderer = new THREE.WebGLRenderer({ alpha: true });
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setAnimationLoop(animate);
    document.body.appendChild(renderer.domElement);
    let points;

    scene.background = new THREE.Color(0x6f7691);
    scene.fog = new THREE.Fog(0x6f7691, 2000, 3500);

    const particles = 500000;

    const g = new THREE.BufferGeometry();

    const positions = [];
    const colors = [];

    const color = new THREE.Color();

    const n = 800, n2 = n / 2;

    for (let i = 0; i < particles; i++) {

      // positions

      const x = Math.random() * n - n2;
      const y = Math.random() * n - n2;
      const z = Math.random() * n - n2;

      positions.push(x, y, z);

      // colors

      const vx = (x / n) + 0.5;
      const vy = (y / n) + 0.5;
      const vz = (z / n) + 0.5;

      color.setRGB(vx, vy, vz, THREE.SRGBColorSpace);

      colors.push(color.r, color.g, color.b);

    }

    g.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));
    g.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));

    g.computeBoundingSphere();


    const m = new THREE.PointsMaterial({ size: 20, vertexColors: true });

    points = new THREE.Points(g, m);
    scene.add(points);

    const geometry = new THREE.BoxGeometry(1, 1, 1);
    const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
    const cube = new THREE.Mesh(geometry, material);
    scene.add(cube);

    function onWindowResize() {

      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();

      renderer.setSize(window.innerWidth, window.innerHeight);

    }

    window.addEventListener('resize', onWindowResize);


    function animate() {

      const time = Date.now() * 0.001;

      points.rotation.x = time * 0.25;
      points.rotation.y = time * 0.5;

      renderer.render(scene, camera);

    }

    // Style the canvas
    renderer.domElement.style.position = 'absolute';
    renderer.domElement.style.top = '0';
    renderer.domElement.style.left = '0';
    renderer.domElement.style.zIndex = '-1';
  }
};
</script>

